@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans', sans-serif; }

html {
  box-sizing: border-box; }

*, *::after*::before {
  box-sizing: inherit; }

body {
  min-width: 320px;
  background-color: #F8F8F8;
  max-width: 1120px;
  margin: auto; }

/* 1 часть Лого и виджеты */
.header {
  background-color: white;
  border-bottom: 1px solid #D9FFF5;
  min-height: 87px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px; }

@media (min-width: 768px) and (max-width: 1119px) {
  .header {
    padding-left: 30px;
    padding-right: 40px; } }

@media (min-width: 1120px) and (max-width: 4000px) {
  .header {
    float: right;
    display: flex;
    width: 420px;
    align-items: center;
    padding: 30px 0 0px;
    height: 60px;
    background-color: #F8F8F8;
    border-bottom: none; } }

/* Переделанный хедер */
.navbar {
  background-image: url(./static/navbar.svg); }

@media (min-width: 768px) and (max-width: 1119px) {
  .logo {
    padding-left: 15px; } }

@media (min-width: 768px) and (max-width: 1119px) {
  .divider, .logo {
    background-image: url(./static/divider.svg);
    background-repeat: no-repeat;
    background-position: left;
    min-width: 2px;
    min-height: 32px; } }

.divider + .logo {
  display: none; }

.divider.dividerSmall {
  background-image: url(./static/divider.svg);
  background-repeat: no-repeat;
  background-position: left;
  min-width: 2px;
  min-height: 32px;
  display: block; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .navbar {
    display: none; } }

.header__line--disappear {
  display: none; }

@media (min-width: 768px) and (max-width: 1119px) {
  .header__line--disappear {
    display: block; } }

.buttonCirlce {
  height: 40px;
  width: 40px;
  background-color: #FF3E79;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer; }

.buttonCirlce__phone {
  background-image: url(./static/phone.svg);
  border: 0; }

.phone--disappear {
  display: none; }

@media (min-width: 768px) and (max-width: 1119px) {
  .phone--disappear {
    display: block; } }

.buttonCirlce__message {
  background-image: url(./static/message1.svg);
  border: 0; }

.message--disappear {
  display: none; }

@media (min-width: 768px) and (max-width: 1119px) {
  .message--disappear {
    display: block; } }

.buttonCirlce__office {
  background-image: url(./static/office.svg);
  border: 0; }

.office--disappear {
  display: none; }

@media (min-width: 768px) and (max-width: 1119px) {
  .office--disappear {
    display: block; } }

.buttonCirlce__settings {
  background-image: url(./static/settings.svg);
  border: 0; }

.text__settings {
  display: none; }

.buttonCirlce__search {
  background-image: url(./static/search.svg);
  border: 0; }

.text__search {
  display: none; }

/* 2 часть Заголовок и скролл */
.heading {
  min-height: 40px;
  display: flex;
  justify-content: space-between;
  padding: 24px 0 20px; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .heading {
    padding: 40px 0 20px; } }

.main {
  background-color: #F8F8F8; }

.content-name {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 30px;
  width: 350px;
  padding-left: 15px;
  letter-spacing: -1px;
  font-weight: bold; }

@media (min-width: 768px) and (max-width: 1119px) {
  .content-name {
    padding-left: 20px; } }

@media (min-width: 1120px) and (max-width: 4000px) {
  .content-name {
    width: 650px; } }

.img {
  height: 45px; }

.scroller {
  display: flex;
  justify-content: center;
  overflow-x: auto; }

.gradient {
  position: absolute;
  top: 10;
  right: 0;
  height: 45px;
  width: 20px;
  background: white;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(248, 248, 248, 0.938813) 70%); }

.link {
  display: inline-block;
  height: 20px;
  color: #7E7E82;
  font-size: 17px;
  padding: 10px 15px 10px 15px;
  text-decoration: none;
  font-weight: 600; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .link {
    padding: 10px 20px 10px 20px; } }

.link:active {
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background-color: white; }

.scroller__items {
  padding: 10px 10px;
  display: flex;
  justify-content: start;
  align-items: stretch;
  overflow-x: auto;
  list-style-type: none;
  white-space: nowrap;
  padding-left: 16px;
  z-index: 0.1; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .scroller__items {
    flex-wrap: wrap; } }

.scroller__items::-webkit-scrollbar {
  display: none; }

ul.scroller__items li.selected a {
  color: #7E7E82;
  border: 2px solid #B8FFEC;
  border-radius: 6px;
  background-color: white; }

ul.scroller__items li a:hover {
  color: #7E7E82; }

.el1-link--mod {
  display: none; }

/* 3 часть контент текст */
.first-paragraph {
  font-size: 16px;
  color: #1B1C21;
  height: 100px;
  padding-top: 5px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 20px;
  text-align: left;
  font-weight: 600; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .first-paragraph {
    display: block;
    font-size: 17px;
    height: 65px; } }

.second-paragraph {
  display: none; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .second-paragraph {
    display: block;
    height: 110px;
    font-size: 17px;
    padding: 0px 16px 0px 16px;
    font-weight: 600; } }

.text-search {
  display: none; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .text-search {
    display: block;
    align-items: center;
    padding-top: 10px;
    float: left;
    height: 24px;
    font-size: 18px;
    padding-right: 20px; } }

.text-settings {
  display: none; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .text-settings {
    display: block;
    align-items: center;
    padding-top: 10px;
    float: left;
    height: 24px;
    font-size: 18px;
    padding-right: 20px; } }

.read-more {
  color: #1B1C21;
  font-size: 18px;
  font-weight: 700;
  background: #F8F8F8;
  display: block;
  border: 0;
  cursor: pointer;
  padding-top: 30px;
  padding-left: 36px; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .read-more {
