.arrow {
  height: 12px;
  padding-right: 20px; }

.picture {
  display: flex;
  justify-content: center;
  padding-top: 20px; }

@media (min-width: 1120px) and (max-width: 4000px) {
  .picture {
    padding-top: 0px; } }

.banner {
  width: 20rem; }

@media (min-width: 768px) and (max-width: 1119px) {
  .banner {
    width: 25rem; } }

/* Часть 4 Ремонт различных брендов */
.brands {
  background-color: #F8F8F8; }

.title__brands {
  font-size: 16px;
  font-weight: 400;
  height: 48px;
  width: 100%;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: -0.2px;
  color: #7E7E82;
  padding: 15px;
  border-top: 1px solid #D9FFF5;
  background: linear-gradient(#F8F8F8, #FFFFFF); }

@media (min-width: 768px) and (max-width: 6000px) {
  .title__brands {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 0 0 24px;
    font-size: 16px;
    font-weight: 400;
    height: 56px;
    width: 100%;
    line-height: 24px;
    text-transform: uppercase;
    letter-spacing: -0.2px;
    color: #7E7E82;
    border-top: 1px solid #D9FFF5;
    background: linear-gradient(#F8F8F8, #FFFFFF); } }

/* swipper */
.image-slider {
  overflow-x: hidden;
  z-index: 0; }

.image-slider .moy_style {
  position: inherit;
  margin-top: 16px; }

.image-slider .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  line-height: 20px;
  font-size: 14px;
  background-color: #B5B6BC; }

.circleArrow {
  background-image: url(./static/arrowone.svg);
  background-repeat: no-repeat;
  background-position: 185px 15px; }

.swiper-wrapper .swiper-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
  min-height: 72px;
  min-width: 240px;
  max-height: 72px;
  max-width: 240px;
  margin-top: 16px;
  background-color: white;
  border: 2px solid #EAEAEA;
  border-radius: 8px; }

.image-slider__image {
  max-height: 39px;
  max-width: 116px;
  padding-left: 16px; }

.apple > .image-slider__image {
  width: 18%;
  height: 44px;
  padding-bottom: 10px; }

.hp > .image-slider__image {
  width: 18%;
  height: 52px; }

.lenovo {
  margin-left: 16px; }

@media (min-width: 768px) and (max-width: 6000px) {
  .lenovo {
    margin-left: 0px; } }

.gradient__swiper {
  position: absolute;
  bottom: 35px;
  height: 75px;
  width: 15px;
  z-index: 1; }

